import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Intervention} from "../metier/intervention.metier";
import {InterventionService} from "../service/intervention.service";
import {BaseComponent} from "../../core/component/base-component";
import {ActivatedRoute, Params} from "@angular/router";
import {ClientService} from "../../client/service/client.service";
import {Client} from "../../client/metier/client.metier";

@Component({
  selector: 'ms-intervention-edit',
  templateUrl: './intervention.edit.component.html',
  styleUrls: ['./intervention.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class InterventionEditComponent extends BaseComponent implements OnInit {

  private intervention: Intervention;
  public interventionForm: FormGroup;
  private typeInterventionList: string[] = [];
  private etatInterventionList: string[] = [];
  private clientList: Array<Client> = [];


  constructor(private fb: FormBuilder, private route: ActivatedRoute,
              private pageTitleService: PageTitleService, private interventionService: InterventionService,
              private clientService: ClientService) {
    super();
  }

  ngOnInit() {
    this.loadIntervention();
    this.pageTitleService.setTitle("MODIFIER UNE CHARGE");
    this.interventionForm = this.fb.group({
      id: [Validators.required],
      date: ['', Validators.required],
      client: ['', Validators.required],
      objet: ['', Validators.required],
      detail: '',
      typeIntervention: ['', Validators.required],
      etatIntervention: ['', Validators.required]
    });
  }


  updateIntervention(interventionForm: any) {
    this.intervention = new Intervention(interventionForm);
    this.interventionService.saveIntervention(this.intervention)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/intervention/list']);
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadIntervention() {
    this.route.params.switchMap((params: Params) => this.interventionService.getIntervention(+params['id']))
      .subscribe(intervention => {
          this.intervention = intervention;
          this.loadTypeInterventionList();
          this.loadEtatInterventionList();
          this.loadClientList();
          this.interventionForm.patchValue(this.intervention);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteIntervention(interventionForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.intervention = new Intervention(interventionForm);
    this.interventionService.deleteIntervention(this.intervention)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/intervention/list']);
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private loadTypeInterventionList() {
    this.interventionService.getTypeInterventionList()
      .subscribe(typeInterventionList => {
          this.typeInterventionList = typeInterventionList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadEtatInterventionList() {
    this.interventionService.getEtatInterventionList()
      .subscribe(etatInterventionList => {
          this.etatInterventionList = etatInterventionList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
          if (this.intervention.client != null) {
            this.intervention.client = this.clientList.find(client => client.id === this.intervention.client.id);
            this.interventionForm.patchValue({client: this.intervention.client}, {onlySelf: true});
          }
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

}






