"use strict";

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Intervention} from "../metier/intervention.metier";
import {FormBuilder, FormGroup} from "@angular/forms";
import {BaseComponent} from "../../core/component/base-component";
import {InterventionService} from "../service/intervention.service";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";

@Component({
  selector: 'ms-intervention-list',
  templateUrl: './intervention.list.component.html',
  styleUrls: ['./intervention.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class InterventionListComponent extends BaseComponent implements OnInit {

  public interventionForm: FormGroup;
  private intervention: Intervention;
  private interventionList: Array<Intervention> = [];
  private clientList: Array<Client> = [];

  constructor(private fb: FormBuilder, private interventionService: InterventionService,
              private pageTitleService: PageTitleService,
              private clientService: ClientService) {
    super();
    this.createForm();

  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES CHARGES");
    this.loadInterventionList();
    this.loadClientList();
  }

  createForm() {
    this.interventionForm = this.fb.group({
      dateDebut: null,
      dateFin: null,
      client: null,
    });
  }

  findInterventionList(interventionForm: any) {
    this.intervention = new Intervention(interventionForm);
    this.interventionService.findInterventionListByCriteria(this.intervention)
      .subscribe(interventionList => {
        this.interventionList = interventionList;
      }, error => {
        this.showError(error.status, error.json().message);
      });
  }

  private loadInterventionList() {
    this.interventionService.getInterventionList()
      .subscribe(interventionList => {
          this.interventionList = interventionList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
        }, error => this.showError(error.status, error.json().message)
      );
  }

}






