import {Routes} from '@angular/router';

import {InterventionAddComponent}  from './intervention.add/intervention.add.component';
import {InterventionListComponent} from "./intervention.list/intervention.list.component";
import {InterventionEditComponent} from "./intervention.edit/intervention.edit.component";

export const InterventionRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: InterventionAddComponent},
    {path: 'list', component: InterventionListComponent},
   {path: 'edit/:id', component: InterventionEditComponent},

  ]
}];



