import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {CustomValidators} from 'ng2-validation';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Intervention} from "../metier/intervention.metier";
import {InterventionService} from "../service/intervention.service";
import {BaseComponent} from "../../core/component/base-component";
import {ClientService} from "../../client/service/client.service";
import {Client} from "../../client/metier/client.metier";


@Component({
  selector: 'ms-intervention-add',
  templateUrl: './intervention.add.component.html',
  styleUrls: ['./intervention.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class InterventionAddComponent extends BaseComponent implements OnInit {

  public interventionForm: FormGroup;
  private intervention: Intervention;
  private typeInterventionList: string[] = [];
  private etatInterventionList: string[] = [];
  private clientList: Array<Client> = [];


  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService,
              private interventionService: InterventionService, private clientService: ClientService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UNE CHARGE");
    this.interventionForm = this.fb.group({
      date: [new Date().toISOString().substring(0, 10), Validators.compose([Validators.required, CustomValidators.date])],
      client: ['', Validators.required],
      objet: ['', Validators.required],
      detail: '',
      typeIntervention: ['', Validators.required],
      etatIntervention: ['', Validators.required]

    });
    this.loadTypeInterventionList();
    this.loadEtatInterventionList();
    this.loadClientList();
  }

  saveIntervention(interventionForm: any) {
    this.intervention = new Intervention(interventionForm);
    this.interventionService.saveIntervention(this.intervention)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(["/intervention/list"]);
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private loadTypeInterventionList() {
    this.interventionService.getTypeInterventionList()
      .subscribe(typePaimentList => {
          this.typeInterventionList = typePaimentList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadEtatInterventionList() {
    this.interventionService.getEtatInterventionList()
      .subscribe(etatPaimentList => {
          this.etatInterventionList = etatPaimentList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  reset() {
    this.interventionForm.reset();
  }


}






