import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {InterventionAddComponent} from "./intervention.add/intervention.add.component";
import {InterventionService} from "./service/intervention.service";
import {InterventionRoutes} from "./intervention.routing";
import {InterventionListComponent} from "./intervention.list/intervention.list.component";
import {InterventionEditComponent} from "./intervention.edit/intervention.edit.component";
import {DataTableModule} from "angular2-datatable";
import {ClientService} from "../client/service/client.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(InterventionRoutes)
  ],
  declarations: [
    InterventionAddComponent,
    InterventionListComponent,
    InterventionEditComponent,
  ],
  providers: [
    InterventionService,
    ClientService
  ],
})

export class InterventionModule {
}



